import React from 'react';
import './Loading2.scss';
class LoadingComponent2 extends React.Component {
    render() {
        return (
            <div className="container loading-component">
                <div className="wrapper33">
                    <div className="loader-outer">
                        <div className="loader-inner">
                            <i className="fa fa-ellipsis-h" aria-hidden="true"></i>
                        </div>
                    </div>
                    <h1 className="h1-loading"><span>LOADING</span></h1>
                </div>
            </div>
        );
    }
}
export default LoadingComponent2;