import React, { Component } from 'react'
import { ProductConsumer } from "../../context";
import { Link } from 'react-router-dom';
import './Detail.scss';
import PostComponent from './Post/Post';
export default class Details extends Component {
  render() {
    return (
        <ProductConsumer>
          {value => {
            const {id, company, img, price, title, info, inCart} = value.detailProduct;

            return (
              <div className="container">
                <div className="post-samsung">
                    <h3 className="title-post-samsung"><Link to="#">{title}</Link></h3>
                    <div>
                        <span className="span-start">
                            <i className="fa fa-star"></i><i className="fa fa-star"></i><i className="fa fa-star"></i>
                            <i className="fa fa-star"></i><i className="fa fa-star"></i>
                        </span>
                        <span>15 customer reviews</span>
                        <span> | 5 comments</span>
                    </div>
                </div>
                <div className="row">
                  <div className="col-md-4 col-sm-6 col-xs-12">
                    <div className="img-details"><img src={img} alt={title} className="img-fluid-detail" /></div>
                  </div>
                  <div className="col-md-4 col-sm-6 col-xs-12">
                    <h3>Model: {title}</h3>
                    <h4 className="">Made by: {company}</h4>
                    <h4>Price: {price}£</h4>
                    <p className="mt-3 mb-0">Info about the product : {info}</p>
                    <div className="">
                      <Link to="/">
                        <button className="back-to-shop">
                          Back
                        </button>
                        </Link>
                        
                        <button
                          cart="true"
                          className="add-to-cart"
                          disabled={inCart? true : false}
                          onClick={() => {
                            value.addToCart(id);
                            value.openModal(id);
                          }}>
                          {inCart ? 'In Cart' : 'Add to Cart'}
                        </button>
                    </div>
                  </div>
                  <div className="col-md-4 col-sm-6 col-xs-12">
                  <h4 className="gifts-range">Gifts when buying products</h4>
                    <p><span className="icon-gift"><i className="fa fa-gift"></i></span><span>Charger, headset, balo, book manual</span></p>
                    <p><span className="icon-gift"><i className="fa fa-car"></i></span><span>Delivery within a week</span></p>
                    <p><span className="icon-gift"><i className="fa fa-shield"></i></span><span>Warranty 12 months</span></p>
                    <p><span className="icon-gift"><i className="fa fa-money"></i></span><span>100% refund when returning products within 24 hours</span></p>
                    <div  className="old-samsung">
                        <div className="color-old-samsung">Old Samsung Galaxy S20</div>
                        <div>Price lower than: 400$</div>
                    </div>
                  </div>
                </div>
                <PostComponent />
              </div>
            );
          }}
        </ProductConsumer>
    )
  }
}