import React from 'react';
import TrendingComponent from './Trending/Trending';
import UpdateNewsComponent from './UpdateNew/updateNew';
import EndNewsComponent from './EndNews/endNews';
import PopularComponent from './Popular/popular';
import LoadingComponent from '../Loading';
class NewsComponent extends React.Component {
    state = {
        loading: true,
      };
      componentDidMount(){
        this.isLoading = setTimeout(()=>{this.setState({loading: false})}, 700);
      }
    render() {
        const {loading} = this.state;
        return (
            loading ? (<LoadingComponent/>)
            :(
            <div className="container">
              <TrendingComponent />
              <UpdateNewsComponent />
              <EndNewsComponent />
              <PopularComponent />
            </div>
            )
        );
    }
}
export default NewsComponent;