import React from 'react'

export default function CartItem({ item, value }) {
  const { id, title, img, price, total, count } = item;
  const { increment, decrement, removeItem } = value;

  return (
    <div className="row myCart-shopping">
      <div className="col-md-4 col-sm-4 col-xs-4 center-img-cartItem">
        <img
          src={img}
          alt={title}
          className="img-fluid Item-cart"
        />
      </div>
      <div className="col-md-4 col-sm-4 col-xs-4">
          <h4 className="title-cartItem">Product: {title}</h4>
          <h5 className="price-cartItem">Price: {price} £</h5>
        <div className="cart-icon">
        <i className="fa fa-trash" onClick={() => removeItem(id)}>Delete</i>
        </div>
        <div className="">
        </div>
      </div>
      {/* Total */}
      <div className="col-md-4 col-sm-4 col-xs-4">
      <div>
            <span
              className="btn btn-black mx-1 quantityproduct"
              onClick={() => { decrement(id) }}>
              -
            </span>
            <span className="btn btn-black mx-1 quantityproduct">
              {count}
            </span>
            <span
              className="btn btn-black mx-1 quantityproduct"
              onClick={() => { increment(id) }}>
              +
            </span>
          </div>
        <h5 className="Item-total">Item total: £{total}</h5>
      </div>
    </div>
  )
}
