import React from 'react';
import './Ourservice.scss';
import SwiperSliderOurService from './Swiper/swiper';
import MyServiceComponent from './OurService/myservice';
import CounterLoadComponent from './Counter/counterload';
import PremiumChatComponent from './PremiumChat/premium';
import HomeSerViceComponent from './HomeService/HomeService';
import LoadingComponent from '../Loading';
class OurServiceComponent extends React.Component {
    state = {
        loading: true,
      };
      componentDidMount(){
        this.isLoading = setTimeout(()=>{this.setState({loading: false})}, 700);
      }
    render() {
        const {loading} = this.state;
        return (
            loading ? (<LoadingComponent/>)
            :(
            <div className="">
                <HomeSerViceComponent />
                <div className="container">
                    <div className="row">
                        <div className="col-md-6">
                            <h2>Welcome to <span className="chap-app-color"> PPHHT</span></h2>
                            <p>Ei mel semper vocent persequeris, nominavi patrioque vituperata id vim, cu eam gloriatur philosophia deterruisset. Meliore perfecto repudiare ea nam, ne mea duis temporibus. Id quo accusam consequuntur, eum ea debitis urbanitas. Nibh reformidans vim ne.</p>
                            <p>Iudico definiebas eos ea, dicat inermis hendrerit vel ei, legimus copiosae quo at. Per utinam corrumpit prodesset te, liber praesent eos an. An prodesset neglegentur qui, usu ancillae posidonium in, mea ex eros animal scribentur. Et simul fabellas sit. Populo inimicus ne est.</p>
                            <p>Alii wisi phaedrum quo te, duo cu alia neglegentur. Quo nonumy detraxit cu, viderer reformidans ut eos, lobortis euripidis posidonium et usu. Sed meis bonorum minimum cu, stet aperiam qualisque eu vim, vide luptatum ei nec. Ei nam wisi labitur mediocrem. Nam saepe appetere ut, veritus graecis minimum no vim. Vidisse impedit id per.</p>
                        </div>
                        <div className="col-md-6 range-about">
                            <SwiperSliderOurService />
                        </div>
                    </div>
                    <MyServiceComponent />
                    <CounterLoadComponent />
                    <PremiumChatComponent />
                </div>
            </div>
            )
        );
    }
}
export default OurServiceComponent;