import React from 'react';
import ViewProductComponent from '../../News/Post/ViewProduct/viewProduct';
import SwiperSamsungOurService from './Header/Header';
import BannerSamsungComponent from './Banner/Banner';
import AllProductComponent from './AllProduct/AllProduct';
import './Samsung.scss';
import LoadingComponent from '../../Loading';
class SPhoneSamsungComponent extends React.Component {
    state = {
        loading: true,
      };
      componentDidMount(){
        this.isLoading = setTimeout(()=>{this.setState({loading: false})}, 700);
      }
    render() {
        const {loading} = this.state;
        return (
            loading ? (<LoadingComponent/>)
            :(
            <div>
                <div className="container">
                    <SwiperSamsungOurService />
                    <BannerSamsungComponent />
                    <AllProductComponent />
                    <ViewProductComponent />
                </div>
            </div>
            )
        );
    }
}
export default SPhoneSamsungComponent;