import React from 'react';
import ViewProductComponent from '../News/Post/ViewProduct/viewProduct';
import SwiperSamsungOurService from './ASUS/Header/Header';
import BannerSamsungComponent from './ASUS/Banner/Banner';
import AllProductComponent from './ASUS/AllProduct/AllProduct';
import LoadingComponent from '../Loading';
class LaptopComponent extends React.Component {
    state = {
        loading: true,
      };
      componentDidMount(){
        this.isLoading = setTimeout(()=>{this.setState({loading: false})}, 700);
      }
    render() {
        const {loading} = this.state;
        return (
            loading ? (<LoadingComponent/>)
            :(
            <div className="container">
                <SwiperSamsungOurService />
                <BannerSamsungComponent />
                <AllProductComponent />
                <ViewProductComponent />
            </div>
            )
        );
    }
}
export default LaptopComponent;