import React from 'react';
import Swiper from 'react-id-swiper';
import 'swiper/css/swiper.css';
import './Icons.scss';
const IconsComponent = () => {
    const params = {
        slidesPerView: 6,
        spaceBetween: 20,
        loop: true,
        autoplay: {
            delay: 2500,
            disableOnInteraction: false
        },
        breakpoints: {
            480: {
                slidesPerView: 3,
                spaceBetween: 10,
            },
            768: {
                slidesPerView: 4,
                spaceBetween: 10,

            },
            992: {
                slidesPerView: 5,
                spaceBetween: 20,
            },
            1200: {
                slidesPerView: 6,
                spaceBetween: 20,
            },
        }
    }

    return (
        <Swiper {...params}>
            <div>
                <img className="img-responsive img-icons" alt="icon2" src={require('./img/asus.png')} />
            </div>
            <div>
                <img className="img-responsive img-icons" alt="icon3" src={require('./img/Dell.png')} />
            </div>
            <div>
                <img className="img-responsive img-icons" alt="icon4" src={require('./img/HP.png')} />
            </div>
            <div>
                <img className="img-responsive img-icons" alt="icon5" src={require('./img/huawei.png')} />
            </div>
            <div><img className="img-responsive img-icons" alt="icon6" src={require('./img/xiaomi.png')} />
            </div>
            <div><img className="img-responsive img-icons" alt="icon7" src={require('./img/new-lenovo-logo.png')} />
            </div>
            <div><img className="img-responsive img-icons" alt="icon8" src={require('./img/oppo.png')} /></div>
            <div><img className="img-responsive img-icons" alt="icon1" src={require('./img/samsung.png')} /></div>
        </Swiper>
    );
}
export default IconsComponent;