import React from 'react';
import './Map.scss';
class MapContactUSComponent extends React.Component {
    render() {
        return (
            <div className="map-contact">
                <div className="map">
                    <iframe className="ifame-google" title="myFrame"
                        src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d7836.915227163078!2d106.7580184647422!3d10.852757018712333!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x31752797e321f8e9%3A0xb3ff69197b10ec4f!2zVHLGsOG7nW5nIENhbyDEkOG6s25nIEPDtG5nIG5naOG7hyBUaOG7pyDEkOG7qWM!5e0!3m2!1svi!2s!4v1570668008144!5m2!1svi!2s"
                    >
                    </iframe>
                </div>
                <div className="container">
                    <div className="row">
                        <div className="col-md-6">
                            <div className="contact-title">
                                <h3>Contacts Us</h3>
                                <p>Contrary to popular belief, Lorem Ipsum is simply random text. It has roots in a piece of
                                    classNameical Latin literature from 45 BC, maki years old.</p>
                            </div>
                            <div className="contact-widget">
                                <div className="cw-item">
                                    <div className="ci-icon">
                                        <i className="fa fa-map-marker"></i>
                                    </div>
                                    <div className="ci-text">
                                        <span>Address:</span>
                                        <p>53 Vo Van Ngan, Thu Duc District</p>
                                    </div>
                                </div>
                                <div className="cw-item">
                                    <div className="ci-icon">
                                        <i className="fa fa-phone"></i>
                                    </div>
                                    <div className="ci-text">
                                        <span>Phone:</span>
                                        <p>(+84)375185272</p>
                                    </div>
                                </div>
                                <div className="cw-item">
                                    <div className="ci-icon">
                                        <i className="fa fa-facebook"></i>
                                    </div>
                                    <div className="ci-text">
                                        <span>Facebook:</span>
                                        <p>https://www.facebook.com/groupPPHHT</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="contact-form">
                                <div className="leave-comment">
                                    <h3>Leave A Comment</h3>
                                    <p>Our staff will call back later and answer your questions.</p>
                                    <form action="#" className="comment-form">
                                        <div className="row">
                                            <div className="col-lg-6">
                                                <input className="outline-contact" type="text" placeholder="Your name" />
                                            </div>
                                            <div className="col-lg-6">
                                                <input className="outline-contact" type="text" placeholder="Your email" />
                                            </div>
                                            <div className="col-lg-12">
                                                <textarea className="outline-contact" placeholder="Your message"></textarea>
                                                <button type="submit" className="site-btn">Send message</button>
                                            </div>
                                        </div>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}
export default MapContactUSComponent;