import React from 'react';
import ViewProductComponent from '../../News/Post/ViewProduct/viewProduct';
import SwiperSamsungOurService from '../Samsung/Header/Header';
import BannerSamsungComponent from '../Samsung/Banner/Banner';
import AllProductComponent from '../Samsung/AllProduct/AllProduct';
import LoadingComponent2 from '../../Loading2';
class SPhoneOppoComponent extends React.Component {
    state = {
        loading: true,
    };
    componentDidMount() {
        this.isLoading = setTimeout(() => { this.setState({ loading: false }) }, 700);
    }
    render() {
        const { loading } = this.state;
        return (
            loading ? (<LoadingComponent2 />)
            : (
            <div className="container">
                <SwiperSamsungOurService />
                <BannerSamsungComponent />
                <AllProductComponent />
                <ViewProductComponent />
            </div>
            )
        );
    }
}
export default SPhoneOppoComponent;