import React from 'react';
import PropTypes from 'prop-types';
import './Promotions.scss';
const PromotionsCountdown = props => {
    return (
        <div className="countdown-promotions">
            <div className="title_post_product">
                <h2>Discount 20% for loyal customers</h2>
            </div>
            <div className="countdown-date">
                <Countdown toDate={new Date(2020, 8, 7)}>
                    {props => (
                        <CountdownDisplay
                            days={props.days}
                            asDays={props.asDays}
                            hours={props.hours}
                            minutes={props.minutes}
                            seconds={props.seconds}
                        />
                    )}
                </Countdown>
            </div>
        </div>
    );
};

const CountdownDisplay = props => {
    const { days, asDays, hours, minutes, seconds } = props;
    return (
        <div className="row countdown-promotions">
            <div className="col-md-6">
                <div className="countdown-timer22" id="countdown">
                <h2>Deal Of The Week</h2>
                <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed<br /> do ipsum dolor sit amet,
                    consectetur adipisicing elit. <br />Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed</p>
                    <time
                        className="CountdownDisplay cd-item"
                        dateTime={`P${asDays}DT${hours}H${minutes}M${seconds}S`}
                    >
                        <span className="CountdownDisplay__days">
                            {String(days).padStart(2, 0) + ' '}
                        </span>
                        <p>Days</p>
                    </time>
                    <time
                        className="CountdownDisplay cd-item"
                        dateTime={`P${asDays}DT${hours}H${minutes}M${seconds}S`}
                    >
                        <span className="CountdownDisplay__hours">
                            {String(hours).padStart(2, 0)}
                        </span>
                        <p>Hours</p>
                    </time>
                    <time
                        className="CountdownDisplay cd-item"
                        dateTime={`P${asDays}DT${hours}H${minutes}M${seconds}S`}
                    >
                        <span className="CountdownDisplay__minutes">
                            {String(minutes).padStart(2, 0)}
                </span>
                        <p>Mins</p>
                    </time>
                    <time
                        className="CountdownDisplay cd-item"
                        dateTime={`P${asDays}DT${hours}H${minutes}M${seconds}S`}
                    >
                        <span className="CountdownDisplay__seconds">
                            {String(seconds).padStart(2, 0)}
                        </span>
                        <p>Secs</p>
                    </time>
                </div>
            </div>
            <div className="col-md-6">
                <div className="btn-jean">
                    <button className="btn-goto"> Go to Shop</button>
                    <button className="btn-goto goto2">Facebook</button>
                </div>
            </div>
        </div>
        
    );
};

CountdownDisplay.propTypes = {
    days: PropTypes.number.isRequired,
    asDays: PropTypes.number.isRequired,
    hours: PropTypes.number.isRequired,
    minutes: PropTypes.number.isRequired,
    seconds: PropTypes.number.isRequired,
};

class Countdown extends React.Component {
    constructor(props) {
        super(props);
        this.state = { now: new Date() };
    }

    componentDidMount() {
        this.intervalId = setInterval(() => {
            this.setState({ now: new Date() });
        }, this.props.intervalDelay);
    }

    componentWillUnmount() {
        clearInterval(this.intervalId);
    }

    render() {
        const { now } = this.state;
        const { toDate } = this.props;
        const delta = toDate - now;

        return this.props.children({
            days: Math.floor((delta / 1000 / 60 / 60 / 24) % 30),
            hours: Math.floor((delta / 1000 / 60 / 60) % 24),
            minutes: Math.floor((delta / 1000 / 60) % 60),
            seconds: Math.floor((delta / 1000) % 60),
            asDays: Math.floor(delta / 1000 / 60 / 60 / 24)
        });
    }
}

Countdown.propTypes = {
    toDate: PropTypes.instanceOf(Date).isRequired,
    intervalDelay: PropTypes.number
};

Countdown.defaultProps = {
    intervalDelay: 1000
};

export default PromotionsCountdown;