import React from 'react';
import './Trending.scss';
import {Link} from 'react-router-dom';


class TrendingComponent extends React.Component {
    render() {
        return (
              <div className="row row-trending">
                    <div className="col-md-9">
                        <div className="image-blogheader">
                            <img className="img-responsive img-logo" alt="blog-1" src={require('../img/samsung1.jpg')}/>
                        </div>
                        <div className="content-blog">
                            <div className="btn-headerblog">
                            <button type="button" className="btn-future">Trending, SamSungS20</button>
                            </div>
                            <h3 className="title-blog-posuere">Posuere at mi a sem</h3>
                            <div className="pad-size"> 
                                <span className="color-blog-posuere">
                                    <span>Posted by </span>
                                    <span><Link to="/news/post">Xuan Phu</Link></span>
                                    <span className="roro">/</span><span>SamSungS20</span>
                                    <span className="roro">/</span><span>40 comments</span>
                                    <span></span>
                                </span>
                            </div>
                           <div className="txt-content">
                           Tortor ac litora phasellus a porta hac vestibulum fringilla taciti gravida 
                           adipiscing est litora sed massa per a ut vivamus libero vel. Ridiculus non et 
                           dis fermentum non libero per hac vestibulum senectus tortor leo nisl lobortis 
                           consectetur senectus habitant facilisi sodales vestibulum potenti nisl a.
                           </div>
                        </div>
                    </div>
                    <div className="col-md-3">
                        <h3 className="categories">
                            <span className="golden-categories">RE</span><span>CENT POSTS</span>
                        </h3>
                        <div>
                            <img className="img-responsive img-blog-categories" alt="img-1" src={require('../img/samsung.png')}/>
                            <div className="content-categories">
                            <Link to="/news/post">Samsung S20</Link>
                            </div>
                            <div className="day-categories">June 25, 2020</div>
                            <div className="border-categories"></div>
                        </div>
                        <div className="img-border-cate">
                            <img className="img-responsive img-blog-categories" alt="img-2" src={require('../img/oppo.jpg')}/>
                            <div className="content-categories"><Link to="/news/post">OPPO Reno3</Link></div>
                            <div className="day-categories">May 25, 2019</div>
                            <div className="border-categories"></div>
                        </div>
                        <div className="img-border-cate">
                            <img className="img-responsive img-blog-categories" alt="img-3" src={require('../img/ipad.jpg')}/>
                            <div className="content-categories"><Link to="/news/post">Ipad Air</Link></div>
                            <div className="day-categories">April 16, 2018</div>
                            <div className="border-categories"></div>
                        </div>
                        <div className="img-border-cate">
                            <img className="img-responsive img-blog-categories" alt="img-4" src={require('../img/iphone.jpg')}/>
                            <div className="content-categories"><Link to="/news/post">Iphone X</Link></div>
                            <div className="day-categories">March 20, 2020</div>
                            <div className="border-categories"></div>
                        </div>
                        <div className="img-border-cate">
                            <img className="img-responsive img-blog-categories" alt="img-5" src={require('../img/huawei.jpg')}/>
                            <div className="content-categories"><Link to="/news/post">Huawei Y9s</Link></div>
                            <div className="day-categories">January 1, 2020</div>
                            <div className="border-categories"></div>
                        </div>
                        <div className="img-border-cate">
                            <img className="img-responsive img-blog-categories" alt="img-5" src={require('../img/dell.png')}/>
                            <div className="content-categories"><Link to="/news/post">Dell N3481</Link></div>
                            <div className="day-categories">September 10, 2019</div>
                            <div className="border-categories"></div>
                        </div>
                        </div>
                </div>
        );
    }
}
export default TrendingComponent;