import React from 'react';
import './updateNew.scss';
import Swiper from 'react-id-swiper';
import 'swiper/css/swiper.css';
const UpdateNewsComponent = () => {
    const params = {
        slidesPerView: 4,
        spaceBetween: 30,
        loop: true,
        navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev',
        },
        breakpoints: {
            480: {
                slidesPerView: 2,
                spaceBetween: 10,
            },
            768: {
                slidesPerView: 3,
                spaceBetween: 10,

            },
            992: {
                slidesPerView: 3,
                spaceBetween: 20,
            },
            1200: {
                slidesPerView: 4,
                spaceBetween: 10,
            },
        }
    }

    return (
        <div className="row row-updatenews">
            <Swiper {...params}>
                <div>
                    <img className="img-responsive img-update" alt="update1" src={require('../img/dell.png')} />
                    <h4 className="title-update">Dell N3481 Sale 20%</h4>
                    <p className="day-categories">Laptop | 2 hour ago</p>
                </div>
                <div>
                    <img className="img-responsive img-update" alt="update2" src={require('../img/ipad.jpg')} />
                    <h4 className="title-update">Ipad Air Sale 10%</h4>
                    <p className="day-categories">Tablet | 3 hour ago</p>
                </div>
                <div>
                    <img className="img-responsive img-update" alt="update3" src={require('../img/oppo.jpg')} />
                    <h4 className="title-update">Oppo Reno Sale 15%</h4>
                    <p className="day-categories">Smartphone | 4 hour ago</p>
                </div>
                <div>
                    <img className="img-responsive img-update" alt="update4" src={require('../img/huawei.jpg')} />
                    <h4 className="title-update">Huawei Y9s Sale 10%</h4>
                    <p className="day-categories">Smartphone | 7 hour ago</p>
                </div>
                <div>
                    <img className="img-responsive img-update" alt="update5" src={require('../img/iphone.jpg')} />
                    <h4 className="title-update">Iphone X Sale 20%</h4>
                    <p className="day-categories">Smartphone | 9 hour ago</p>
                </div>
                <div>
                    <img className="img-responsive img-update" alt="update6" src={require('../img/hp.jpg')} />
                    <h4 className="title-update">HP Pavilion Sale 20%</h4>
                    <p className="day-categories">Laptop | 12 hour ago</p>
                </div>
                <div>
                    <img className="img-responsive img-update" alt="update6" src={require('../img/asus.jpg')} />
                    <h4 className="title-update">Asus Zenbook Sale 20%</h4>
                    <p className="day-categories">Laptop | 20 hour ago</p>
                </div>
            </Swiper>
        </div>

    );
}
export default UpdateNewsComponent;