import React from 'react';
import Swiper from 'react-id-swiper';
import 'swiper/css/swiper.css';
import './swiper-params.scss';

const SwiperSliderOurService = () => {
    const params = {
    loop: true,
      autoplay: {
      delay: 2000,
      disableOnInteraction: false,
    },
      pagination: {
        el: '.swiper-pagination',
        clickable: true,
        dynamicBullets: true
      },
      navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev',
      }
    }
    return (
      <Swiper {...params}>
        <div><img className="img-responsive img-logo-menu" alt="menu1" src={require('../img/item1.jpg')}/></div>
        <div><img className="img-responsive img-logo-menu" alt="menu1" src={require('../img/item2.jpg')}/></div>
        <div><img className="img-responsive img-logo-menu" alt="menu1" src={require('../img/item3.jpg')}/></div>
        <div><img className="img-responsive img-logo-menu" alt="menu1" src={require('../img/item4.jpg')}/></div>
        <div><img className="img-responsive img-logo-menu" alt="menu1" src={require('../img/item5.jpg')}/></div>
      </Swiper>
    )
  };
export default SwiperSliderOurService;