import React from 'react';
import './Banner.scss';
import Swiper from 'react-id-swiper';
import 'swiper/css/swiper.css';
const SwiperSSComponent = () => {
    const params = {
        slidesPerView: 5,
        spaceBetween: 10,
        loop: true,
        navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev',
        },
        breakpoints: {
            240: {
                slidesPerView: 2,
                spaceBetween: 20,
            },
            480: {
                slidesPerView: 3,
                spaceBetween: 10,
            },
            768: {
                slidesPerView: 3,
                spaceBetween: 10,

            },
            992: {
                slidesPerView: 4,
                spaceBetween: 20,
            },
            1200: {
                slidesPerView: 5,
                spaceBetween: 10,
            },
        }
    }
    return (
        <div className="samsung-galaxy">
            <Swiper {...params}>
                <div className="samsung-top10">
                    <img className="img-responsive" alt="update1" src={require('../img/dell2.jpg')} />
                    <h4 className="title-update center-text">Dell Inspiron 14 N7490</h4>
                    <p className="day-categories center-text">Price: 700$</p>
                    <div className="sale-samsung">-20%</div>
                </div>
                <div className="samsung-top10">
                    <img className="img-responsive" alt="update1" src={require('../img/dell5.jpg')} />
                    <h4 className="title-update center-text">Dell Vostro V5490 i7</h4>
                    <p className="day-categories center-text">Price: 700$</p>
                </div>
                <div className="samsung-top10">
                    <img className="img-responsive" alt="update1" src={require('../img/dell4.jpg')} />
                    <h4 className="title-update center-text">Dell Inspiron N5490</h4>
                    <p className="day-categories center-text">Price: 700$</p>
                    <div className="sale-samsung">-10%</div>
                </div>
                <div className="samsung-top10">
                    <img className="img-responsive" alt="update1" src={require('../img/hp6.jpg')} />
                    <h4 className="title-update center-text">HP 15s-fq1116TU</h4>
                    <p className="day-categories center-text">Price: 700$</p>
                    <div className="sale-samsung">-20%</div>
                </div>
                <div className="samsung-top10">
                    <img className="img-responsive" alt="update1" src={require('../img/hp7.jpg')} />
                    <h4 className="title-update center-text">HP ENVY 13-ba0046TU</h4>
                    <p className="day-categories center-text">Price: 700$</p>
                    <div className="sale-samsung">-10%</div>
                </div>
                <div className="samsung-top10">
                    <img className="img-responsive" alt="update1" src={require('../img/mac3.png')} />
                    <h4 className="title-update center-text">MacBook Pro 13 2020</h4>
                    <p className="day-categories center-text">Price: 700$</p>
                </div>
                <div className="samsung-top10">
                    <img className="img-responsive" alt="update1" src={require('../img/mac7.jpg')} />
                    <h4 className="title-update center-text">Macbook Air 13 128GB</h4>
                    <p className="day-categories center-text">Price: 700$</p>
                </div>
                <div className="samsung-top10">
                    <img className="img-responsive" alt="update1" src={require('../img/asus5.jpg')} />
                    <h4 className="title-update center-text">ASUS Expertbook B9450FA</h4>
                    <p className="day-categories center-text">Price: 700$</p>
                </div>
                <div className="samsung-top10">
                    <img className="img-responsive" alt="update1" src={require('../img/asus2.jpg')} />
                    <h4 className="title-update center-text">Asus VivoBook M413IA</h4>
                    <p className="day-categories center-text">Price: 700$</p>
                    <div className="sale-samsung">-10%</div>
                </div>
                <div className="samsung-top10">
                    <img className="img-responsive" alt="update1" src={require('../img/hp4.jpg')} />
                    <h4 className="title-update center-text">New HP 12 256GB Gold</h4>
                    <p className="day-categories center-text">Price: 700$</p>
                </div>
            </Swiper>
        </div>
    );
}
class BannerSamsungComponent extends React.Component {
    render() {
        return (
            <div>
                <h3 className="top10-phone">Top10 best selling phones</h3>
                <div className="banner-samsung">
                    <SwiperSSComponent />
                </div>
            </div>
        );
    }
}
export default BannerSamsungComponent;