import React from 'react';
import Swiper from 'react-id-swiper';
import 'swiper/css/swiper.css';
import './Header.scss';
import {Link} from 'react-router-dom';
const SwiperSamsungOurService = () => {
    const params = {
        loop: true,
        autoplay: {
            delay: 3000,
            disableOnInteraction: false,
        },
        pagination: {
            el: '.swiper-pagination',
            clickable: true,
            dynamicBullets: true
        },
        navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev',
        }
    }
    return (
        <div className="header-phone">
            <Swiper {...params}>
                <div><img className="img-responsive img-header" alt="menu1" src={require('../img/bg4.jpg')} /></div>
                <div><img className="img-responsive img-header" alt="menu1" src={require('../img/bg5.png')} /></div>
                <div><img className="img-responsive img-header" alt="menu1" src={require('../img/bg6.jpg')} /></div>
                <div><img className="img-responsive img-header" alt="menu1" src={require('../img/bg3.png')} /></div>
            </Swiper>
            <div className="filter">
                <div className="manu manu7">
                   <Link to="#">
                        <img className="img-responsive" alt="manu1" src={require('../img/asus.jpg')}></img>
                    </Link>
                   <Link to="#">
                        <img className="img-responsive" alt="manu2" src={require('../img/Dell.png')}></img>
                    </Link>
                   <Link to="#">
                        <img className="img-responsive" alt="manu3" src={require('../img/HP.png')}></img>
                    </Link>
                   <Link to="#">
                        <img className="img-responsive" alt="manu4" src={require('../img/huawei.png')}></img>
                    </Link>
                   <Link to="#">
                        <img className="img-responsive" alt="manu5" src={require('../img/iphone.png')}></img>
                    </Link>
                   <Link to="#">
                        <img className="img-responsive" alt="manu6" src={require('../img/oppo.png')}></img>
                    </Link>
                   <Link to="#">
                        <img className="img-responsive" alt="manu7" src={require('../img/lenovo.png')}></img>
                    </Link>
                </div>
            </div>
        </div>
    )
};
export default SwiperSamsungOurService;