import React from 'react';
import './Main.scss';
import MenuComponent from './Components/Menu/Menu';
import FooterComponent from './Components/Footer/footer';
import IconsComponent from './Components/Icons/Icons';
import Skeleton, { SkeletonTheme } from 'react-loading-skeleton';
class Loading extends React.Component {
    render() {
        return (
            <div className="backgr-welcome">
                <div className="nav-skeleton"><SkeletonTheme color="#03c4eb" highlightColor="#81dce1">
                    <Skeleton height={40} /></SkeletonTheme></div>
                <div className="container">
                    <div className="row">
                        <SkeletonTheme color="#abadac" highlightColor="#bababa">
                            <div className="col-md-12 col-sm-12 col-xs-12 skeleton-nav">
                                <Skeleton height={230} />
                            </div>
                            <div className="col-md-12 col-sm-12 col-xs-12 skeleton-title">
                                <Skeleton width={350} height={30} />
                            </div>
                            <div className="col-md-3 col-sm-4 col-xs-6 skeleton-loading2">
                                <Skeleton height={200} width={160} />
                                <Skeleton className="skeleton-tittle" count={2} height={20} width={220} />
                            </div>
                            <div className="col-md-3 col-sm-4 col-xs-6 skeleton-loading2">
                                <Skeleton height={200} width={160} />
                                <Skeleton className="skeleton-tittle" count={2} height={20} width={220} />
                            </div>
                            <div className="col-md-3 col-sm-4 col-xs-6 skeleton-loading2">
                                <Skeleton height={200} width={160} />
                                <Skeleton className="skeleton-tittle" count={2} height={20} width={220} />
                            </div>
                            <div className="col-md-3 col-sm-4 col-xs-6 skeleton-loading2">
                                <Skeleton height={200} width={160} />
                                <Skeleton className="skeleton-tittle" count={2} height={20} width={220} />
                            </div>

                        </SkeletonTheme>
                    </div>
                </div>
            </div>
        );
    }
}
class MainComponent extends React.Component {
    state = {
        loading: true,
    };
    componentDidMount() {
        this.isLoading = setTimeout(() => { this.setState({ loading: false }) }, 2000);
        this.state = {
            // modal should be closed on page load
            isModalOpen: false
        };
        // binding methods
        this.openModal = this.openModal.bind(this);
        this.closeModal = this.closeModal.bind(this);
    }

    openModal() {
        this.setState({ isModalOpen: true })
    }

    closeModal() {
        this.setState({ isModalOpen: false })
    }

    render() {
        const { loading } = this.state;
        return (
            loading ? (<Loading />)
                : (<div className="content">
                    <div className="">
                        <MenuComponent />
                        <IconsComponent />
                        <FooterComponent />
                        <div className="play-icon" onClick={this.openModal}>
                            <i className="fa fa-comment play-icon-chat">
                            </i>
                        </div>
                        <div className="modal-message"><Modal isOpen={this.state.isModalOpen} onClose={this.closeModal} /></div>
                    </div>
                </div>)
        );
    }
}
class Modal extends React.Component {
    render() {
        const { isOpen, onClose } = this.props;
        return (
           
            <div className={isOpen ? 'modal modal--is-open' : 'modal'}>
                <div className="modal-message">
                <div className="popup-chat">
                    <div className="bg-popup-chat">
                    <span className="img-admin-chat"><img className="img-responsive" alt="lienhe"src={require('./img/admin.png')} /></span>
                    <span><button className="btn-closeMessage" onClick={onClose}>X</button></span>
                    <span className="online-message"></span>
                    <span className="contact-admin"></span>
                    </div>
                    <div className="chat-app">
                        <ChatApp/>
                    </div>
                </div>
                </div>
            </div>
            
        );
    }
}


class ChatApp extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
                     messages: [
                     {id:'1',text:'Hi Admin!', me:true},
                     {id:'2',text:'Hello Thanos!', me:false},
                     {id:'3',text:'Where are you from?', me:false},
                     {id:'4',text:'Lorem ipsum dolor sit amet', me:true},
                     {id:'5',text:'Have you ever heard of lorem ipsum?', me:true},
                     {id:'6',text:'Lorem ipsum dolor sit amet, consectetur adipiscing elit', me:false},
                     {id:'7',text:'Lorem ipsum dolor sit amet, ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in.', me:true},
                     {id:'8',text:'Welcome to projects!!', me:false}
                    ],
                     message: ''
                   };
      this.handleChange = this.handleChange.bind(this);
      this.handleSubmit = this.handleSubmit.bind(this);
    }
  
    render() {
      return (
        <div className="app-message">
        <div className="app">
          <div className="messages">
            <div className="messages-history">
              <MessagesHistory items={this.state.messages} />
            </div>
            <form className="messages-inputs" onSubmit={this.handleSubmit}>
              <input type="text" placeholder="Send a message" onChange={this.handleChange} value={this.state.message}/>
                <button className="btn-sendMessage"><i className="material-icons">send</i></button>
            </form>
          </div>
        </div>
        </div>
      );
    }
  
    handleChange(e) {
      this.setState({ message: e.target.value });
    }
  
    handleSubmit(e) {
      e.preventDefault();
      if (!this.state.message.length) {
        return;
      }
      const newItem = {
        text: this.state.message,
        id: Date.now(),
        me: true
      };
      this.setState(state => ({
        messages: state.messages.concat(newItem),
        message: ''
      }));
    }
  }
  
  class MessagesHistory extends React.Component {
    render() {
      return [].concat(this.props.items).reverse().map(item => (
          <div className={"message " + (item.me ? "me" : "")} key={item.id}>
            <div className="message-body">
              {item.text}
            </div>
          </div>
        ));
    }
  }
export default MainComponent;