import React from 'react';
import './inforProduct.scss';
import {Link} from 'react-router-dom';
import Swiper from 'react-id-swiper';
import 'swiper/css/swiper.css';
const SlideSamsungComponent = () => {
    const params = {
        loop: true,
        navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev',
        }
    }

    return (
        <div className="slide-samsung">
            <Swiper {...params}>
                <div>
                    <img className="img-responsive img-update" alt="update1" src={require('../../img/samsung.png')} />
                </div>
                <div>
                    <img className="img-responsive img-update" alt="update2" src={require('../../img/s20.jpg')} />
                </div>
                <div>
                    <img className="img-responsive img-update" alt="update3" src={require('../../img/s20.png')} />
                </div>
                <div>
                    <img className="img-responsive img-update" alt="update4" src={require('../../img/s202.jpg')} />
                </div>
                <div>
                    <img className="img-responsive img-update" alt="update5" src={require('../../img/s203.png')} />
                </div>
            </Swiper>
        </div>
    );
}
class InforProductComponent extends React.Component {
    render() {
        return (
            <div>
                <div className="post-samsung">
                    <h3 className="title-post-samsung"><Link to="#">Samsung Galaxy S20 - N4572CSJD</Link></h3>
                    <div>
                        <span className="span-start">
                            <i className="fa fa-star"></i><i className="fa fa-star"></i><i className="fa fa-star"></i>
                            <i className="fa fa-star"></i><i className="fa fa-star"></i>
                        </span>
                        <span>15 customer reviews</span>
                        <span> | 3 comments</span>
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-4 col-sm-6 col-xs-12">
                        <SlideSamsungComponent />
                    </div>
                <div className="col-md-4 col-sm-6 col-xs-12 col-samsung-padding">
                    <div className="row">
                        <div className="col-md-6 col-sm-6 col-xs-6 buy-now-samsung">
                            <div className="color-old-samsung">Buying online</div>
                            <div>800$</div>
                        </div>
                        <div className="col-md-6 col-sm-6 col-xs-6 buy-now-samsung">
                            <div className="color-old-samsung">Price</div>
                            <div>1000$</div>
                        </div>
                    </div>
                    <div className="row">
                        <div className="special-samsung">Special offers (limited quantity)</div>
                        <div className="description-samsung">
                            <p className="text-description">Ridiculus non et dis fermentum non libero per hac vestibulum senectus tortor leo nisl lobortis consectetur</p>
                            <p className="text-description">Tortor ac litora phasellus a porta hac vestibulum fringilla</p>
                            <p className="text-description">Taciti gravida adipiscing est litora sed massa per a ut vivamus</p>
                        </div>
                    </div>
                    <div className="row">
                        <button className="buynow-samsung">
                            <div className="buynow-ss">Buy Now</div>
                            <div>Delivery in 1 hour or received at the shop</div>
                        </button>
                        <button className="btn-left-buynow">
                            Installment 0%
                        </button>
                        <button className="btn-right-buynow">
                            Payment by card
                        </button>
                    </div>
                </div>
                <div className="col-md-4 col-sm-6 col-xs-12">
                    <h4>Gifts when buying products</h4>
                    <p><span className="icon-gift"><i className="fa fa-gift"></i></span><span>Charger, headset, balo, book manual</span></p>
                    <p><span className="icon-gift"><i className="fa fa-car"></i></span><span>Delivery within a week</span></p>
                    <p><span className="icon-gift"><i className="fa fa-shield"></i></span><span>Warranty 12 months</span></p>
                    <p><span className="icon-gift"><i className="fa fa-money"></i></span><span>100% refund when returning products within 24 hours</span></p>
                    <div  className="old-samsung">
                        <div className="color-old-samsung">Old Samsung Galaxy S20</div>
                        <div>Price lower than: 400$</div>
                    </div>
                </div>
                </div>
            </div>
        );
    }
}
export default InforProductComponent;