import React from 'react';
import './Home.scss';
import HeaderComponent from './Header/Header';
import PromotionsCountdown from './Promotions/Promotions';
import LoadingComponent from '../Loading';
import ProductList from '../Detail/ProductList';
class HomeComponent extends React.Component{
  state = {
    loading: true,
  };
  componentDidMount(){
    setTimeout(()=>{this.setState({loading: false})}, 700);
  }
  render() {
    const {loading} = this.state;
    return(
      loading ? (<LoadingComponent/>)
      :(
      <div className="container">
          <HeaderComponent />
          <div className="row rvt-alert-home">
            <div className="col-md-3 col-sm-4 col-xs-6 non-alert">
            <Alert2
                title="Samsung galxaxy"
                id="01"
                children="Ridiculus non et dis fermentum non consectet"
                img={require("./img/ss4.jpg")}
                buy="Buy Now"
                />
            </div>
            <div className="col-md-3 col-sm-4 col-xs-6 none-alert-sm">
              <div className="alert-notification">
                <Alert2
                    title="Huawei P30 Pro"
                    id="02"
                    children="Lorem ipsum dolor amet offal butcher quinoa"
                    img={require("./img/hw6.jpg")}
                    buy="Buy Now"
                    />
              </div>
            </div>
            <div className="col-md-3 col-sm-4 col-xs-6">
            <Alert2
                title="Huawei Nova 5T"
                id="03"
                children="Tortor leo nisl lobortis consectetu tortor leo nisl lobortis"
                img={require("./img/hw8.jpg")}
                buy="Buy Now"
                />
            </div>
            <div className="col-md-3 col-sm-4 col-xs-6">
            <Alert2
                title="Oppo Reno 3"
                id="04"
                children="Tote bag wolf authentic try-hard put a bird on it mumblecore"
                img={require("./img/op3.jpg")}
                buy="Buy Now"
                />
            </div>
          </div>
          <PromotionsCountdown />
          <ProductList />
      </div>
      )
    );
  }
}

class Alert2 extends React.Component {
  constructor(props) {
      super(props);
      this.state = { isDismissed: false }
      this.dismissAlert = this.dismissAlert.bind(this);
  }
  
  dismissAlert() {
      this.setState({
          isDismissed: true
      });
  }

  render() {
      const {children, title, img, id, buy} = this.props;

      return this.state.isDismissed ? null : (
          <div className="rvt-alert2"
               role="alertdialog"
               aria-labelledby={id}>
              <h3 className="rvt-alert_title2" id={id}>{ title }<span> 
                <button className="rvt-alert_dismiss2" onClick={this.dismissAlert}>
                  X
                </button>
              </span></h3>
              <p className="rvt-alert_message2">{ children }</p>
              <img src={img} className="img-responsive img-home-alert"></img>
              <button className="buy-alert">
              <span></span>
              <span></span>
              <span></span>
              <span></span>
              {buy}</button>
          </div>
      );
  }
}
export default HomeComponent;