import React from 'react';
import ViewProductComponent from '../../News/Post/ViewProduct/viewProduct';
import SwiperSamsungOurService from '../Huawei/Header/Header';
import AllProductComponent from '../Huawei/AllProduct/AllProduct';
import BannerSamsungComponent from '../Huawei/Banner/Banner';
import LoadingComponent2 from '../../Loading2';
class IpadComponent extends React.Component {
    state = {
        loading: true,
      };
      componentDidMount(){
        this.isLoading = setTimeout(()=>{this.setState({loading: false})}, 700);
      }
    render() {
        const {loading} = this.state;
        return (
            loading ? (<LoadingComponent2/>)
            :(
            <div className="container">
                <SwiperSamsungOurService />
                <BannerSamsungComponent />
                <AllProductComponent />
                <ViewProductComponent />
            </div>
            )
        );
    }
}
export default IpadComponent;