import React from 'react';
import './HomeService.scss';
class HomeSerViceComponent extends React.Component {
    render() {
        return (
            <div>
                <div className="hero">
                    <div className="text-hero">
                    <h2>Group PPHHT</h2>
                    <h2>Group PPHHT</h2>
                    </div>
                     
                    <div className="highway"></div>
                    <div className="city"></div>
                    <div className="car">
                    <img src={require('../img/car.png')} alt="" className="img-responsive" />
                    </div>
                    <div className="wheel">
                    <img src={require('../img/wheel.png')} alt="" className="back-wheel" />
                    </div>
                    <div className="wheel">
                    <img src={require('../img/wheel.png')} alt="" className="front-wheel" />
                    </div>
                </div>
            </div>
        );
    }
}
export default HomeSerViceComponent;