import React from 'react';
import Swiper from 'react-id-swiper';
import 'swiper/css/swiper.css';
import './Header.scss';


const Accordion = ({ title, children }) => {
  const [isOpen, setOpen] = React.useState(false);
  return (
    <div className="accordion-wrapper">
      <div
        className={`accordion-title ${isOpen ? "open" : ""}`}
        onClick={() => setOpen(!isOpen)}
      >
        {title}
      </div>
      <div className={`accordion-item ${!isOpen ? "collapsed" : ""}`}>
        <div className="accordion-content">{children}</div>
      </div>
    </div>
  );
};
const AppAccordion = () => (
  <div className="wrapper">
    <Accordion title="Best selling laptop">
      <div>1. MacBook Pro 13 2020</div>
      <div>2. Dell V3590 i5</div>
      <div>3. HP Pavilion 14-ce3026TU</div>
      <div>4. Asus Vivobook A412D</div>
      <div>5. Dell Inspiron N3593C</div>
    </Accordion>
    <Accordion title="Best selling tablet">
      <div>1. Samsung Galaxy Tab A8</div>
      <div>2. iPad 2019 10.2</div>
      <div>3. Huawei MediaPad T5</div>
      <div>4. iPad Pro 11 2020</div>
      <div>5. Samsung Galaxy Tab S6 Lite</div>
    </Accordion>
    <Accordion title="Best selling smartphone">
      <div>1. Samsung Galaxy A21s</div>
      <div>2. Iphone X</div>
      <div>3. OPPO Find X2</div>
      <div>4. Huawei Enjoy 20 Pro</div>
      <div>5. Iphone 8 plus</div>
    </Accordion>
  </div>
);

const SwiperHeaderHome = () => {
  const params = {
    loop: true,
    autoplay: {
      delay: 3000
    },
    pagination: {
      el: '.swiper-pagination',
      clickable: true,
      dynamicBullets: true
    },
    navigation: {
      nextEl: '.swiper-button-next',
      prevEl: '.swiper-button-prev',
    }
  }
  return (
    <div className="header-phone">
      <Swiper {...params}>
        <div><img className="img-responsive img-header" alt="menu1" src={require('../img/bg1.jpg')} /></div>
        <div><img className="img-responsive img-header" alt="menu1" src={require('../img/bg3.jpg')} /></div>
        <div><img className="img-responsive img-header" alt="menu1" src={require('../img/bg4.jpg')} /></div>
        <div><img className="img-responsive img-header" alt="menu1" src={require('../img/bg5.png')} /></div>
        <div><img className="img-responsive img-header" alt="menu1" src={require('../img/bg6.jpg')} /></div>
        <div><img className="img-responsive img-header" alt="menu1" src={require('../img/bg10.jpg')} /></div>
      </Swiper>
    </div>
  )
};
class HeaderComponent extends React.Component {
  render() {
    return (
      <div className="row">
        <div className="col-md-9">
          <SwiperHeaderHome />
        </div>
        <div className="col-md-3 range-home">
            <div className="title_post_product arcodion-home">
                <h2>Selling products</h2>
            </div>
          <AppAccordion />
          <div className="alert-message">
            <Alert
                title="Thank you!"
                children="We come to project!!! Tortor ac litora phasellus a porta hac vestibulum fringilla taciti gravida adipiscing est litora sed">
                
            </Alert>
        </div>
        </div>
      </div>
    );
  }
}

class Alert extends React.Component {
  constructor(props) {
      super(props);
      this.state = { isDismissed: false }
      this.dismissAlert = this.dismissAlert.bind(this);
  }

  dismissAlert() {
      this.setState({
          isDismissed: true
      });
  }

  render() {
      const {children, title} = this.props;

      return this.state.isDismissed ? null : (
          <div className="rvt-alert rvt-alert"
               role="alertdialog">
              <h3 className="rvt-alert_title">{ title }<span> 
                <button className="rvt-alert_dismiss" onClick={this.dismissAlert}>
                  X
                </button>
              </span></h3>
              <p className="rvt-alert_message">{ children }</p>
          </div>
      );
  }
}
export default HeaderComponent;