import React from 'react';
import ViewProductComponent from './ViewProduct/viewProduct';
import DetailComponent from './DetailProduct/detail';
import './Post.scss';


class PostComponent extends React.Component {
    constructor() {
      super();
      this.state = {
        showComments: false,
        comments: [
          {id: 1, author: "Xuan Phu", body: "Ridiculus non et dis fermentum non libero per hac vestibulum"},
          {id: 2, author: "Minh Hiep", body: "That's a mighty fine comment you've got there my good looking fellow..."},
          {id: 3, author: "Quynh Huong", body: "It won’t be a bigger problem to find one video game lover in your neighbor."},
          {id: 4, author: "Lam Phat", body: "Tortor leo nisl lobortis consectetu tortor leo nisl lobortis consectet"},
          {id: 5, author: "Minh Thuan", body: "Taciti gravida adipiscing est litora sed massa per a ut vivamus"}
        ]
      };
    }
    
    render () {
      const comments = this.getComments();
      let commentNodes;
      let buttonText = 'Show Comments';
      
      if (this.state.showComments) {
        buttonText = 'Hide Comments';
        commentNodes = <div className="comment-list">{comments}</div>;
      }
      
      return(
        <div className="container">
        <div className="row">
          <div className="col-md-9">
            <div className="body-post">
              <h3>Outstanding characteristics</h3>
              <p>Ridiculus non et dis fermentum non libero per hac vestibulum senectu
                s tortor leo nisl lobortis consectetu tortor leo nisl lobortis consectet
                Ridiculus non et dis fermentum non libero per hac vestibulum senectu
                s tortor leo nisl lobortis consectetu tortor leo nisl lobortis consectet</p>
                <img className="img-responsive img-post-body" alt="img-1" src={require('../img/samsung.png')} />
              <details className="details-post-body">
              <summary>
                <span id="open">View more</span> 
                <span id="close">Close</span> 
              </summary>
              <h3>Product details</h3>
              <p>Ridiculus non et dis fermentum non libero per hac vestibulum senectu
                s tortor leo nisl lobortis consectetu tortor leo nisl lobortis consectet
                Ridiculus non et dis fermentum non libero per hac vestibulum senectu
                s tortor leo nisl lobortis consectetu tortor leo nisl lobortis consectet</p>
              <img className="img-responsive img-post-body" alt="img-1" src={require('../img/s20.png')} />
              <h3>Product reviews</h3>
              <p>Ridiculus non et dis fermentum non libero per hac vestibulum senectu
                s tortor leo nisl lobortis consectetu tortor leo nisl lobortis consectet
                Ridiculus non et dis fermentum non libero per hac vestibulum senectu
                s tortor leo nisl lobortis consectetu tortor leo nisl lobortis consectet</p>
              <img className="img-responsive img-post-body" alt="img-1" src={require('../img/s202.jpg')} />
              </details>
            </div>
            <div className="comment-box">
              <h3 className="below-comments">Comments below</h3>
              <CommentForm addComment={this.addComment.bind(this)}/>
              <button id="comment-reveal" onClick={this.handleClick.bind(this)}>
                {buttonText}
              </button>
              <h4 className="comment-count">
                {this.getCommentsTitle(comments.length)} <span> | Click the "Show comments" button to view the comment</span>
              </h4>
              {commentNodes}
            </div>
          </div>
          <div className="col-md-3">
            <DetailComponent />
          </div>
        </div>
        <ViewProductComponent />
        </div>
      );
    } // end render
    
    addComment(author, body) {
      const comment = {
        id: this.state.comments.length + 1,
        author,
        body
      };
      this.setState({ comments: this.state.comments.concat([comment]) }); // *new array references help React stay fast, so concat works better than push here.
    }
    
    handleClick() {
      this.setState({
        showComments: !this.state.showComments
      });
    }
    
    getComments() {    
      return this.state.comments.map((comment) => { 
        return (
          <Comment 
            author={comment.author} 
            body={comment.body} 
            key={comment.id} />
        ); 
      });
    }
    
    getCommentsTitle(commentCount) {
      if (commentCount === 0) {
        return 'No comments yet';
      } else if (commentCount === 1) {
        return "1 comment";
      } else {
        return `${commentCount} comments`;
      }
    }
  } // end CommentBox component
  
  class CommentForm extends React.Component {
    render() {
      return (
        <form className="comment-form" onSubmit={this.handleSubmit.bind(this)}>
          <div className="comment-form-fields">
            <input className="input-comments" placeholder="Enter your name" required ref={(input) => this._author = input}></input><br />
            <textarea className="text-area-comments" placeholder="Write a comment..." rows="4" required ref={(textarea) => this._body = textarea}></textarea>
          </div>
          <div className="comment-form-actions">
            <button className="btn-postComments" type="submit">Post Comment</button>
          </div>
        </form>
      );
    } // end render
    
    handleSubmit(event) { 
      event.preventDefault();   // prevents page from reloading on submit
      let author = this._author;
      let body = this._body;
      this.props.addComment(author.value, body.value);
    }
  } // end CommentForm component
  
  class Comment extends React.Component {
    render () {
      return(
        <div className="container">        
          <div className="row">
            <div className="col-md-9 comments-bg">
                <img className="img-responsive img-user-comment" alt="img-1" src={require('../img/user.png')} />
                <h4 className="comment-header">{this.props.author}</h4>
                <p className="comment-body">{this.props.body}</p>
            </div>
          </div>
        </div>
      );
    }
  }

export default PostComponent;