import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { ProductConsumer } from '../../context';

export default class Product extends Component {
	render() {
		const { id, title, img, price, inCart } = this.props.product;

		return (
			<div className="col-sm-4 col-xs-6 col-md-3 col-lg-3">
				<div className="card-pdshopping mb-3">
					<ProductConsumer>
						{(value) => (
							<div
								className="img-container-cart p-5"
								onClick={() => {
									value.handleDetail(id);
								}}
							>
								<Link to="/details">
									<img src={img} alt={title} className="card-img-top" />
								</Link>
								<button
									className="cart-btn-shopping"
									disabled={inCart ? true : false}
									onClick={() => {
										value.addToCart(id);
									}}
								>
									{inCart ? (
										<div className="text-capitalize mb-0 small" disabled>
											{''}
											 In Cart
										</div>
									) : (
										<div><i className="fa fa-shopping-cart" /></div>
									)}
								</button>
							</div>
						)}
					</ProductConsumer>

					{/* Card footer */}
					<div className="card-footer d-flex justify-content-between">
						<h5 className="align-self-center mb-0">Model: {title}</h5>
						<h5 className="mb-0">
							Price: {price}£
						</h5>
					</div>
				</div>
			</div>
		);
	}
}

// Set up PropTypes to check for propTypes and make them required
Product.propTypes = {
	product: PropTypes.shape({
		id: PropTypes.number,
		img: PropTypes.string,
		title: PropTypes.string,
		price: PropTypes.number
		//inCart: PropTypes.boolean
	})
};
