import React from 'react';
import './viewProduct.scss';
import Swiper from 'react-id-swiper';
import 'swiper/css/swiper.css';
const ViewProductComponent = () => {
    const params = {
        slidesPerView: 5,
        spaceBetween: 10,
        loop: true,
        navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev',
        },
        breakpoints: {
            240: {
                slidesPerView: 2,
                spaceBetween: 20,
            },
            480: {
                slidesPerView: 3,
                spaceBetween: 10,
            },
            768: {
                slidesPerView: 3,
                spaceBetween: 10,

            },
            992: {
                slidesPerView: 4,
                spaceBetween: 20,
            },
            1200: {
                slidesPerView: 5,
                spaceBetween: 10,
            },
        }
    }

    return (
        <div className="row row-post-product">
            <div className="title_post_product">
                <h2>Product you have viewed</h2>
            </div>
            <Swiper {...params}>
                <div>
                    <img className="img-responsive img-update" alt="update1" src={require('../../img/dell.png')} />
                    <h4 className="title-update center-text">Dell N3481 Sale 20%</h4>
                    <p className="day-categories center-text">Price: 700$</p>
                </div>
                <div>
                    <img className="img-responsive img-update" alt="update2" src={require('../../img/ipad.jpg')} />
                    <h4 className="title-update center-text">Ipad Air Sale 10%</h4>
                    <p className="day-categories center-text">Price: 600$</p>
                </div>
                <div>
                    <img className="img-responsive img-update" alt="update3" src={require('../../img/oppo.jpg')} />
                    <h4 className="title-update center-text">Oppo Reno Sale 15%</h4>
                    <p className="day-categories center-text">Price: 400$</p>
                </div>
                <div>
                    <img className="img-responsive img-update" alt="update4" src={require('../../img/huawei.jpg')} />
                    <h4 className="title-update center-text">Huawei Y9s Sale 10%</h4>
                    <p className="day-categories center-text">Price: 500$</p>
                </div>
                <div>
                    <img className="img-responsive img-update" alt="update5" src={require('../../img/iphone.jpg')} />
                    <h4 className="title-update center-text">Iphone X Sale 20%</h4>
                    <p className="day-categories center-text">Price: 1000$</p>
                </div>
                <div>
                    <img className="img-responsive img-update" alt="update6" src={require('../../img/hp.jpg')} />
                    <h4 className="title-update center-text">HP Pavilion Sale 20%</h4>
                    <p className="day-categories center-text">Price: 900$</p>
                </div>
                <div>
                    <img className="img-responsive img-update" alt="update6" src={require('../../img/asus.jpg')} />
                    <h4 className="title-update center-text">Asus Zenbook Sale 20%</h4>
                    <p className="day-categories center-text">Price: 850$</p>
                </div>
            </Swiper>
        </div>

    );
}
export default ViewProductComponent;