import React, { Fragment } from 'react'
import { Link } from 'react-router-dom';

export default function CartTotals({value}) {
    const { cartSubTotal, clearCart } = value;

  return (
    <Fragment>
      <div className="container subandclearCart">
        <Link to="/cart">
              <button 
                  className="btnClearAllCart"
                  type="button"
                  onClick={() => clearCart()}>
                      Clear Cart
              </button>
          </Link>
          <h5 className="subtotal-cart">Subtotal: {cartSubTotal}£
          </h5>
      </div>
    </Fragment>
  )
}
