import React from 'react';
import './counterload.scss';
import CountUp from 'react-countup';

class CounterLoadComponent extends React.Component {

    render() {
        return (
            <div>
                <div className="container jumbotron">
                <h3 className="service-about2">About Us</h3>
                    <p className="title-p2">At vero eos et accusamus et iusto odio dignissimos ducimus qui blanditiis praesentium</p>
                    <div className="row w-100">
                        <div className="col-md-3">
                            <div className="card border-info mx-sm-1 p-3">
                                <div className="card border-info shadow text-info p-3 my-card" ><span className="fa fa-user" aria-hidden="true"></span></div>
                                <div className="text-info text-center mt-3"><h4 className="mt-h4">Employees</h4></div>
                                <div className="text-info text-center mt-2"><h3 className="counter">
                                    <CountUp end={64} duration={3}/></h3></div>
                            </div>
                        </div>
                        <div className="col-md-3 col-counter">
                            <div className="card border-warning mx-sm-1 p-3">
                                <div className="card border-warning shadow text-warning p-3 my-card" ><span className="fa fa-inbox" aria-hidden="true"></span></div>
                                <div className="text-warning text-center mt-3"><h4 className="mt-h4">Product</h4></div>
                                <div className="text-warning text-center mt-2"><h3 className="counter">
                                <CountUp end={3456} duration={3}/></h3></div>
                            </div>
                        </div>
                        <div className="col-md-3 col-counter">
                            <div className="card border-success mx-sm-1 p-3">
                                <div className="card border-success shadow text-success p-3 my-card"><span className="fa fa-eye" aria-hidden="true"></span></div>
                                <div className="text-success text-center mt-3"><h4 className="mt-h4">View</h4></div>
                                <div className="text-success text-center mt-2"><h3 className="counter">
                                <CountUp end={6498} duration={2}/></h3></div>
                            </div>
                        </div>
                        <div className="col-md-3 col-counter">
                            <div className="card border-danger mx-sm-1 p-3">
                                <div className="card border-danger shadow text-danger p-3 my-card" ><span className="fa fa-heart" aria-hidden="true"></span></div>
                                <div className="text-danger text-center mt-3"><h4 className="mt-h4">Like Page</h4></div>
                                <div className="text-danger text-center mt-2"><h3 className="counter">
                                <CountUp end={2064} duration={2}/></h3></div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        );
    }
}


export default CounterLoadComponent;